
.progressslider-wrapper {
  width: 100%;
  position: relative;
  height: 24px;
  margin-bottom: 16px;
  display: flex;
  cursor: default;
  outline: none;
}
.progressslider-wrapper .progressslider-track {
  width: 100%;
  position: absolute;
  height: 2px;
  left: 0;
  top: 50%;
  margin-top: -1px;
  background-color: #bdbdbd;
}
.progressslider-wrapper .progressslider-fill {
  position: absolute;
  height: 2px;
  left: 0;
  top: 50%;
  margin-top: -1px;
  background-color: #ce3d3e;
}
.progressslider-wrapper .progressslider-thumb {
  position: absolute;
  top: 50%;
  width: 12px;
  height: 12px;
  background-color: #ce3d3e;
  color: #ce3d3e;
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: background 0.45s cubic-bezier(0.23, 1, 0.32, 1), border-color 0.45s cubic-bezier(0.23, 1, 0.32, 1), width 0.45s cubic-bezier(0.23, 1, 0.32, 1), height 0.45s cubic-bezier(0.23, 1, 0.32, 1);
  transition: background 0.45s cubic-bezier(0.23, 1, 0.32, 1), border-color 0.45s cubic-bezier(0.23, 1, 0.32, 1), width 0.45s cubic-bezier(0.23, 1, 0.32, 1), height 0.45s cubic-bezier(0.23, 1, 0.32, 1);
  cursor: pointer;
}


