.search {
  background: #d43c33;
  height: 46px;
  display: flex;
}
.search .yuyin {
  flex-basis: 40px;
  background: url("../../../static/img/music.png") no-repeat;
  background-position: center center;
  background-size: 32px 32px;
}
.search .input {
  flex: 1;
}
.search .input input {
  width: 80%;
  height: 30px;
  border-radius: 5px;
  margin-top: 8px;
  color: #c6c7c9;
  font-size: 12px;
  padding-left: 60px;
}
.search .music {
  flex-basis: 40px;
  text-align: center;
}
.search .music span {
