  color: #fff;
}
.search .music img {
  width: 20px;
  height: 20px;
  margin-left: 10px;
  margin-top: 13px;
}
.searchresult {
  position: absolute;
  top: 46px;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
  z-index: 10;
}
.searchresult .list-ul li {
  display: flex;
}
.searchresult .list-ul li .img {
  flex-basis: 48px;
  text-align: center;
  line-height: 56px;
  color: #999;
}
.searchresult .list-ul li .img.active {
  color: #d33a31;
}
.searchresult .list-ul li .title {
  flex: 1;
  position: relative;
}
.searchresult .list-ul li .title:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid #ddd;
  width: 100%;
