  color: #333;
}
.searchresult .list-ul li .title .music-name.active {
  color: #d33a31;
}
.searchresult .list-ul li .title p {
  font-size: 16px;
  color: #949494;
}
.searchresult .list-ul li .title p i {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-image: url("../../../static/img/sq.png");
  background-position: center center;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  vertical-align: middle;
}
.searchresult .list-ul li .title p span {
  vertical-align: middle;
  font-size: 12px;
}
.searchresult .list-ul li .title p span.active {
  color: #d33a31;
}
.searchresult .list-ul li .menu {
  flex-basis: 48px;
  margin-top: 12px;
  position: relative;
}
.searchresult .list-ul li .menu:after {
  display: block;
