  bottom: 0;
  border-top: 1px solid #ddd;
  width: 100%;
  content: '';
}
.searchresult .list-ul li .menu .menu-img {
  width: 36px;
  height: 26px;
  background-position: center center;
  background-size: 18px 18px;
  background-repeat: no-repeat;
  border: 1px solid #d3d4da;
  border-radius: 4px;
}
.searchresult .list-ul li .menu:nth-child(3) .menu-img {
  background-image: url("../../../static/img/aee.png");
}
.searchresult .list-ul li .menu:nth-child(4) .menu-img {
  background-image: url("../../../static/img/more.png");
}


