}
.musicsong-wrapper.fade-enter-active,
.musicsong-wrapper.fade-leave-active {
  transition: all 0.2s linear;
  transform: translate3d(0, 0, 0);
}
.musicsong-wrapper.fade-enter,
.musicsong-wrapper.fade-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.musicsong-wrapper .menu-title {
  display: flex;
  position: relative;
}
.musicsong-wrapper .menu-title:after {
  display: block;
  position: absolute;
  left: 0;
